import * as React from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';

const steps = [
  'Raise Request',
  'Verification of admin',
  'Approved',
];

export default function HorizontalLabelPositionBelowStepperApproved() {
  return (
    <Box sx={{ width: '100%' }} color='green'>
      <Stepper sx={{ bgcolor: 'green' }} activeStep={2} alternativeLabel>
        {steps.map((label, index) => (
          <Step sx={{
            '& .MuiStepLabel-root .Mui-completed': {
              color: 'secondary.dark', // circle color (COMPLETED)
            },
            '& .MuiStepLabel-label.Mui-completed.MuiStepLabel-alternativeLabel':
              {
                color: 'grey.500', // Just text label (COMPLETED)
              },
            '& .MuiStepLabel-root .Mui-active': {
              color: 'lime', // circle color (ACTIVE)
            },
            '& .MuiStepLabel-label.Mui-active.MuiStepLabel-alternativeLabel':
              {
                color: 'common.green', // Just text label (ACTIVE)
              },
            '& .MuiStepLabel-root .Mui-active .MuiStepIcon-text': {
              fill: 'black', // circle's number (ACTIVE)
            },
          }} key={label}>
            <StepLabel
              StepIconProps={{
                classes: { completed: 'MuiStepIcon-completed' },
              }}
              classes={{
                completed: 'MuiStepLabel-completed',
              }}
            >
              {label}
            </StepLabel>
          </Step>
        ))}
      </Stepper>
    </Box>
  );
}